.item {
  list-style-type: none;
  display: -ms-flexbox;
  display: flex;
  border-bottom: 1px solid lightgray;
  padding: 10px;
  margin: 20px;
}

.item:last-child {
  border: none;
}

.item .textContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 0 20px;
}

.item h2 {
  margin: 0;
}

.item p {
  margin: 0;
}



